/* Retina icons */
@media only screen and (max-width: 700px) and (-webkit-min-device-pixel-ratio: 2),
only screen and (max-width: 700px) and (min--moz-device-pixel-ratio: 2),
only screen and (max-width: 700px) and (-o-min-device-pixel-ratio: 2/1),
only screen and (max-width: 700px) and (min-device-pixel-ratio: 2) {
    #osu_navbar .univ_name a {background-image: url($vendor-images + '/osu-navbar/white/osu_name@2x.png') !important;
    /* scale name background size */
    /* -webkit-background-size: 100px 16px; -moz-background-size: 100px 16px; */ background-size: contain;}
  #osu_navbar div.links ul li a.help {background-image:url($vendor-images + '/osu-navbar/white/resp-help@2x.png') !important;}
  #osu_navbar div.links ul li a.buckeyelink {background-image:url($vendor-images + '/osu-navbar/white/resp-buckeyelink@2x.png') !important;}
  #osu_navbar div.links ul li a.map {background-image:url($vendor-images + '/osu-navbar/white/resp-map@2x.png') !important;}
  #osu_navbar div.links ul li a.findpeople {background-image:url($vendor-images + '/osu-navbar/white/resp-findpeople@2x.png') !important;}
  #osu_navbar div.links ul li a.webmail {background-image:url($vendor-images + '/osu-navbar/white/resp-webmail@2x.png') !important;}
  #osu_navbar div.links ul li a.search {background-image:url($vendor-images + '/osu-navbar/white/resp-search@2x.png') !important;}

  /* scale icon background size */
  #osu_navbar div.links ul li a.help,
  #osu_navbar div.links ul li a.buckeyelink,
  #osu_navbar div.links ul li a.map,
  #osu_navbar div.links ul li a.findpeople,
  #osu_navbar div.links ul li a.webmail,
  #osu_navbar div.links ul li a.search { -webkit-background-size: 23px; -moz-background-size: 23px; background-size: 23px;}

}

