// General variable structure
//
// Variable format should follow the `$component-modifier-state-property` order.


// Colors
//
// Grayscale and brand colors for use across Bootstrap.

$gray-dark:                 #212121;
$gray:                      #55595c;
$gray-light:                #666666; // Custom grey (used for .text-muted)
$gray-lighter:              #c2c2c2; // #eceeef;
$gray-lightest:             #eceeef; // #f7f7f9;

$brand-primary:             #26686d; // OSU Blue
$brand-success:             #38ae67; // UCR Green
$brand-info:                #3283c8; // Link Blue
$brand-warning:             #fc8a51; // UCR Warning
$brand-danger:              #e52c5a; // UCR Pink
$brand-inverse:             $gray-dark;


// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-flex:               false;

// Body
//
// Settings for the `<body>` element.

$body-bg:    #fff;
$body-color: $gray-dark;

// Links
//
// Style anchor elements.

$link-color:                 #3283c8; // JQuery Mobile UI framework's link color
$link-decoration:            none; // Removed, we'll be overriding with a border-bottom
$link-hover-color:           darken($link-color, 15%);
$link-hover-decoration:      none; // Removed, we'll be overriding with a border-bottom

// Typography
//
// Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:     "ProximaNova", sans-serif;

// Pixel value used to responsively scale all typography. Applied to the `<html>` element.
$font-size-root:             16px;

$font-size-base:             1rem;
$font-size-lg:               1.25rem;
$font-size-sm:               .8rem;
$font-size-xs:               .8rem;

$font-size-h1:               2.5rem;
$font-size-h2:               2rem;
$font-size-h3:               1.75rem;
$font-size-h4:               1.5rem;
$font-size-h5:               1.25rem;
$font-size-h6:               1rem;

$display1-size:               6rem;
$display2-size:               5.5rem;
$display3-size:               4.5rem;
$display4-size:               3.5rem;

$display1-weight:             300;
$display2-weight:             300;
$display3-weight:             300;
$display4-weight:             300;

$line-height:                1.5;

$headings-font-family:       inherit;
$headings-font-weight:       300;
$headings-line-height:       1.1;
$headings-color:             inherit;

$lead-font-size:             1.25rem;
$lead-font-weight:           300;

// Components
//
// Define common padding and border radius sizes and more.

$line-height-lg:         (4 / 3);
$line-height-sm:         1.5;

$border-radius:          3px;
$border-radius-lg:       3px;
$border-radius-sm:       3px;

$component-active-color: #fff;
$component-active-bg:    $brand-primary;

// Buttons
//
// For each of Bootstrap's buttons, define text, background and border color.

$btn-secondary-color:         $gray;

// Labels

$label-default-bg:            #bb0000; // OSU red

$label-color:                 #fff;
$label-font-weight:           300;

// Form validation icons, removed as we don't inline error icons to forms
$form-icon-success: none;
$form-icon-warning: none;
$form-icon-danger: none;

// Forms
$input-color:                   $body-color; // Darker input text color
$input-border-color:            $gray-light; // Darker border

$input-bg-disabled:              $gray-lightest;

$input-group-addon-bg:           $gray-lightest;
$input-group-addon-border-color: $input-border-color;

// $input-border-focus:            #7da4a7; // OSU PMS 5473 60% tint
// $custom-select-focus-border-color: #7da4a7; // OSU PMS 5473 60% tint

$custom-control-checked-indicator-bg: $brand-primary;
$custom-checkbox-indeterminate-bg: $brand-primary;

$custom-control-gutter:   1.5rem;
$custom-control-spacer-x: 2rem;
$custom-control-spacer-y: 1rem;

// Form states and alerts
//
// Define colors for form feedback states and, by default, alerts.

$state-success-text:             $body-color;
$state-success-bg:               lighten($brand-success, 99% - lightness($brand-success));
$state-success-border:           $brand-success;

$state-info-text:                $gray-light;
$state-info-bg:                  transparent;
$state-info-border:              #9a9a9a;

$state-warning-text:             $body-color;
$state-warning-bg:               lighten($brand-warning, 99% - lightness($brand-warning));
$state-warning-border:           $brand-warning;

$state-danger-text:              $body-color;
$state-danger-bg:                lighten($brand-danger, 99% - lightness($brand-danger));
$state-danger-border:            $brand-danger;

